import { Injectable } from '@angular/core';
import { Headers, Http, RequestOptions } from '@angular/http';

import 'rxjs/add/operator/toPromise';

import { Energy } from './energy';

@Injectable()
export class HttpApiService {
    constructor(private http: Http) { }

    getEnergy(stu_id: string, builing: string, room_id: string): Promise<Energy> {
        const url = `http://api.dogest.cn/grade/energy/query?id=${ stu_id }&room=${ builing }${ room_id }`;
        return this.http.get(url)
            .toPromise()
            .then(response => response.json() as Energy)
            .catch(this.handleError);
    }
    send_check_email(energy: Energy, email: string): Promise<boolean> {
        const url = `/check_email/${ email }/`;
        const body = JSON.stringify(energy.data);
        return this.http.post(url, body)
            .toPromise()
            .then((response) => true);
    }
    send_submit_email(stu_id: string, builing: string, room_id: string, email: string): Promise<boolean> {
        const url = `/submit_email/${ email }/`;
        const body = JSON.stringify({
            'id': stu_id,
            'room': builing + room_id
        });
        return this.http.post(url, body)
            .toPromise()
            .then(response => true);
    }
    private handleError(error: any): Promise<any> {
        console.error('An error occurred', error);
        return Promise.reject(error.message || error);
    }
}



