import { Component, Input, OnInit } from '@angular/core';

import { AreaMap, Energy } from './energy';
import { HttpApiService } from './http-api.service';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent {
    energy: Energy;
    title = '宿舍电量查询';
    stu_id: string;
    room_id: string;
    select_area: string;
    select_building: string;
    areaMap: AreaMap;
    area = [];
    builing = [];
    result: Energy;
    energy_error: boolean;
    ave_down: number; // 平均每日使用下限
    ave_up: number; // 平均每日使用上限
    less_day: number; // 预计最少可使用天数
    more_day: number; // 预计最多可用天数
    apply: boolean; // 标记是否开启申请
    check: boolean; // 标记是否验证过
    email: string; // 邮箱地址
    constructor(private httpApiService: HttpApiService) {
        this.stu_id = '';
        this.room_id = '';
        this.area = ['西校区', '东校区'];
        this.select_area = this.area[0];
        this.areaMap = new AreaMap();
        this.builing = this.areaMap[this.select_area];
        this.energy_error = false;
        this.apply = false;
        this.check = false;
        this.email = '';
    }
    drawData(energy): void {
        this.energy = energy;
        if (this.energy.code !== 0) {
            this.energy_error = true;
        } else {
            if (energy.data.weekaverage > energy.data.monthaverage) {
                this.ave_down = energy.data.monthaverage;
                this.ave_up = energy.data.weekaverage;
            } else {
                this.ave_up = energy.data.monthaverage;
                this.ave_down = energy.data.weekaverage;
            }
            this.less_day = Math.floor(energy.data.lastreading / this.ave_up);
            this.more_day = Math.ceil(energy.data.lastreading / this.ave_down);
            this.energy_error = false;
        }
    }
    search(): void {
        this.close();
        this.httpApiService.getEnergy(this.stu_id, this.select_building, this.room_id)
            .then(energy => this.drawData(energy));
    }
    changeArea(event): void {
        this.builing = this.areaMap[event];
    }
    close(): void {
        this.energy = null;
        this.energy_error = false;
        this.apply = false;
        this.check = false;
        this.email = '';
    }
    check_email(): void {
        this.httpApiService.send_check_email(this.energy, this.email)
            .then(flag => this.check = flag);
    }
    submit_email(): void {
        this.httpApiService.send_submit_email(this.stu_id, this.select_building, this.room_id, this.email)
            .then(flag => this.close());
    }
}



